    const detailContent = ref('');
    const detailTit = ref('');
    function showDetail(time, content) {
      detailTit.value = time;
      detailContent.value = content;
      centerDialogVisible.value = true;
    }
    return {
      leftHanlde,
      rightHanlde,
      lineLeft,
      leftStr,
      timeLineWrapper,
      data,
      centerDialogVisible,
      detailTit,
      detailContent,
      showDetail
